"use client";

import { useState } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import Link from "next/link";

export default function CadastroPage() {
  const [formData, setFormData] = useState({
    nome: "",
    email: "",
    senha: "",
    tipo: "",
  });

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    console.log("Dados enviados:", formData);
    alert("Cadastro realizado com sucesso!");
  };

  return (
    <div className="flex items-center justify-center min-h-screen bg-background text-foreground">
      <Card className="w-full max-w-md p-8 flex flex-col items-center gap-6">
        <div className="text-center">
          <h1 className="text-3xl font-bold text-accent">CE Sports</h1>
          <h2 className="text-lg mt-1 text-muted-foreground">
            Cadastro de Usu√°rio
          </h2>
        </div>

        <form onSubmit={handleSubmit} className="w-full flex flex-col gap-4">
          <Input
            name="nome"
            type="text"
            placeholder="Nome completo"
            required
            value={formData.nome}
            onChange={handleChange}
          />

          <Input
            name="email"
            type="email"
            placeholder="E-mail institucional"
            required
            value={formData.email}
            onChange={handleChange}
          />

          <Input
            name="senha"
            type="password"
            placeholder="Senha"
            required
            value={formData.senha}
            onChange={handleChange}
          />

          <select
            name="tipo"
            required
            value={formData.tipo}
            onChange={handleChange}
            className="flex h-10 w-full rounded-md border border-border bg-background px-3 py-2 text-sm text-primary focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-accent focus-visible:ring-offset-2"
          >
            <option value="" disabled>
              Selecione seu tipo de usu√°rio
            </option>
            <option value="aluno">Aluno</option>
            <option value="administrador">Administrador</option>
          </select>

          <Button type="submit" className="w-full">
            Cadastrar
          </Button>
        </form>

        <div className="flex flex-col items-center gap-2 text-sm">
          <Link href="/" className="text-accent hover:underline">
            üè† Ir para Home
          </Link>
          <Link href="/login" className="text-accent hover:underline">
            Login
          </Link>
        </div>
      </Card>
    </div>
  );
}
